const UsersList = (data) => {

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.attendance)

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  // ** States
  const [sort, setSort] = useState('desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn, setSortColumn] = useState('createdon')
  const [rowsPerPage, setRowsPerPage] = useState(50)
  const [currentStatus] = useState({ value: '', label: 'Select Status', number: 0 })
  const [startDate, setStartDate] = useState(moment().startOf('day').startOf('month').valueOf())
  const [endDate, setEndDate] = useState(moment().startOf('day').endOf('month').valueOf())

  // ** Get data on mount
  useEffect(async () => {
    setBlock(true)
    if (data.tabId === 'monthlysummary') {
      await dispatch(
        getMonthlySummary({
          sort,
          sortColumn,
          q: searchTerm,
          page: currentPage,
          perPage: rowsPerPage,
          userId,
          status: currentStatus.value,
          start: startDate,
          end: endDate
        })
      )
    }
    setBlock(false)
  }, [data.tabId])

  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getMonthlySummary({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: rowsPerPage,
        page: page.selected + 1,
        userId,
        status: currentStatus.value,
        start: startDate,
        end: endDate
      })
    )
    await setBlock(false)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = async e => {
    const value = parseInt(e.currentTarget.value)
    setBlock(true)
    await dispatch(
      getMonthlySummary({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: value,
        page: currentPage,
        userId,
        status: currentStatus.value,
        start: startDate,
        end: endDate
      })
    )
    setBlock(false)
    setRowsPerPage(value)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getMonthlySummary({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        userId,
        status: currentStatus.value,
        start: startDate,
        end: endDate
      })
    )
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      userId,
      status: currentStatus.value,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.monthlySummaries.length > 0) {
      return store.monthlySummaries
    } else if (store.monthlySummaries.length === 0 && isFiltered) {
      return []
    } else {
      return store.monthlySummaries.slice(0, rowsPerPage)
    }
  }

  const handleSort = async (column, sortDirection) => {
    setSort(sortDirection)
    setSortColumn(column.sortField)
    setBlock(true)
    await dispatch(
      getMonthlySummary({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        userId,
        status: currentStatus.value,
        start: startDate,
        end: endDate
      })
    )
    setBlock(false)
  }

  const getList = async (val) => {
    let start_date, end_date
    setBlock(true)
    if (val === 1) {
      start_date = moment(startDate, 'x').subtract(1, 'M').startOf('M').startOf('D').valueOf()
      end_date = moment(endDate, 'x').subtract(1, 'M').endOf('M').startOf('D').valueOf()
      setStartDate(start_date)
      setEndDate(end_date)
    } else {
      start_date = moment(startDate, 'x').add(1, 'M').startOf('m').startOf('D').valueOf()
      end_date = moment(endDate, 'x').add(1, 'M').endOf('m').startOf('D').valueOf()
      setStartDate(start_date)
      setEndDate(end_date)
    }
    await dispatch(
      getMonthlySummary({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        userId,
        status: currentStatus.value,
        start: startDate,
        end: endDate
      })
    )
    await setBlock(false)
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <div className='react-dataTable'>
            <DataTable
              noHeader
              subHeader
              sortServer
              pagination
              responsive
              paginationServer
              columns={columns}
              onSort={handleSort}
              sortIcon={<ChevronDown />}
              highlightOnHover
              className='react-dataTable'
              paginationComponent={CustomPagination}
              data={dataToRender()}
              subHeaderComponent={
                <CustomHeader
                  store={store}
                  getList={getList}
                  startDate={startDate}
                  searchTerm={searchTerm}
                  rowsPerPage={rowsPerPage}
                  handleFilter={handleFilter}
                  handlePerPage={handlePerPage}
                />
              }
            />
          </div>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default UsersList
