// ** User List Component
import AdminView from './AdminView'
import ProfileView from './ProfileView'

// ** Styles
import '@styles/react/apps/app-users.scss'
import { Nav, NavItem, TabContent, TabPane, NavLink } from 'reactstrap'
import { useState } from 'react'
import { activeOrganization } from '@src/helper/sassHelper'
const TeamList = () => {

  const [active, setActive] = useState('profileview')
  const activeOrg = activeOrganization()
  const toggle = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  return (
    <div>
      {
        (parseInt(activeOrg.rolelevel) === 1 || parseInt(activeOrg.rolelevel) === 2) &&
        <div className='app-user-list'>

          <Nav tabs className='nav-left'>
            <NavItem>
              <NavLink
                active={active === 'profileview'}
                onClick={() => {
                  toggle('profileview')
                }}
              >
                Profile View
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                active={active === 'adminview'}
                onClick={() => {
                  toggle('adminview')
                }}
              >
                Team View
              </NavLink>
            </NavItem>
          </Nav>
          <TabContent activeTab={active}>
            <TabPane tabId='profileview'>
              <ProfileView tabId={active} />
            </TabPane>
            <TabPane tabId='adminview'>
              <AdminView tabId={active} />
            </TabPane>
          </TabContent>
        </div>
      }
      {
        (parseInt(activeOrg?.rolelevel) !== 1 && parseInt(activeOrg?.rolelevel) !== 2) && <ProfileView tabId={active} />
      }

    </div>
  )
}

export default TeamList
