// ** React Imports
import { Fragment, useState, useEffect } from 'react'

// ** Table Columns
import { columns } from './columns'

// ** Store & Actions
import { getDailyList, exportDailyList } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import classnames from 'classnames'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { ChevronDown, Download, MoreVertical } from 'react-feather'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Reactstrap Imports
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import {
  Row,
  Col,
  Card,
  Input,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown,
  Button,
  Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import moment from 'moment'

// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm, attendanceDate, handleDateFilter, exportData }) => {
  // ** Converts table to CSV

  return (
    <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
      <Row>
        <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
          <div className='d-flex align-items-center w-100'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              className='mx-50'
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: '5rem' }}
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
            <label htmlFor='rows-per-page'>Entries</label>
          </div>
        </Col>
        <Col md='6' sm='6'
          xl='6'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
            <label className='mb-0' htmlFor='search-invoice'>
              Search:
            </label>
            <Input
              id='search-invoice'
              className='ms-50 w-100'
              type='text'
              value={searchTerm}
              onChange={e => handleFilter(e.target.value)}
            />
          </div>

          <div className='d-flex align-items-center table-header-actions'>
            <Flatpickr
              value={String(attendanceDate / 1000)}
              onChange={(date, dateStr) => { handleDateFilter(dateStr * 1000) }}
              options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
              className={classnames('due-date-picker')} />
            <UncontrolledDropdown className='ms-1'>
              <DropdownToggle color='secondary' caret outline>
                <MoreVertical className='font-small-4 me-50' />
              </DropdownToggle>
              <DropdownMenu>
                <DropdownItem className='w-100' onClick={() => exportData()}>
                  <Download className='font-small-4 me-50' />
                  <span className='align-middle'>Export</span>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </div>
        </Col>
      </Row>
    </div>
  )
}

const UsersList = (data) => {

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.attendance)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  // ** States
  const [sort, setSort] = useState('desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn, setSortColumn] = useState('createdon')
  const [rowsPerPage, setRowsPerPage] = useState(50)
  const [currentStatus] = useState({ value: '', label: 'Select Status', number: 0 })
  const [attendanceDate, setAttendanceDate] = useState(moment().startOf('day').utc().format('x'))

  const [formModal, setFormModal] = useState(false)
  const closeModal = () => {
    setFormModal(false)
  }
  useEffect(() => {
    setFormModal(false)
  }, [])

  // ** Get data on mount
  useEffect(async () => {
    setBlock(true)
    if (data.tabId === 'todayattendance') {
      await dispatch(
        getDailyList({
          sort,
          sortColumn,
          q: searchTerm,
          page: currentPage,
          perPage: rowsPerPage,
          status: currentStatus.value,
          date: attendanceDate
        })
      )
    }
    setBlock(false)
  }, [dispatch, store.data.length, sort, sortColumn, currentPage, data.tabId])

  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getDailyList({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: rowsPerPage,
        page: page.selected + 1,
        status: currentStatus.value,
        date: attendanceDate
      })
    )
    setCurrentPage(page.selected + 1)
    setBlock(false)
  }

  // ** Function in get data on rows per page
  const handlePerPage = async e => {
    setBlock(true)
    const value = parseInt(e.currentTarget.value)
    await dispatch(
      getDailyList({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: value,
        page: currentPage,
        status: currentStatus.value,
        date: attendanceDate
      })
    )
    setBlock(false)
    setRowsPerPage(value)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getDailyList({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.value,
        date: attendanceDate
      })
    )
    setBlock(false)
  }

  const handleDateFilter = async val => {
    setAttendanceDate(val)
    setBlock(true)
    await dispatch(
      getDailyList({
        sort,
        q: searchTerm,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.value,
        date: val
      })
    )
    setBlock(false)
  }

  const exportData = async () => {

    await dispatch(
      exportDailyList({
        sort,
        q: searchTerm,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.value,
        date: attendanceDate,
        export: true
      })
    )
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      status: currentStatus.value,
      date: moment().startOf('day').utc().format('x'),
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    }
  }

  const handleSort = async (column, sortDirection) => {
    setSort(sortDirection)
    setBlock(true)
    setSortColumn(column.sortField)
    dispatch(
      getDailyList({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.value,
        date: attendanceDate
      })
    )
    setBlock(false)
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <div className='react-dataTable'>
            <DataTable
              noHeader
              subHeader
              sortServer
              pagination
              responsive
              paginationServer
              highlightOnHover
              columns={columns}
              onSort={handleSort}
              sortIcon={<ChevronDown />}
              className='react-dataTable'
              paginationComponent={CustomPagination}
              data={dataToRender()}
              subHeaderComponent={
                <CustomHeader
                  store={store}
                  searchTerm={searchTerm}
                  rowsPerPage={rowsPerPage}
                  handleFilter={handleFilter}
                  handlePerPage={handlePerPage}
                  attendanceDate={attendanceDate}
                  handleDateFilter={handleDateFilter}
                  exportData={exportData}
                />
              }
            />
          </div>
        </Card>
      </UILoader>
      <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
        <ModalHeader toggle={() => closeModal()}>Attendance</ModalHeader>

        <ModalBody>

        </ModalBody>
        <ModalFooter>
          <Button color='warning' onClick={() => closeModal()}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal >

    </Fragment>
  )
}

export default UsersList
