
import FullCalendar from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
// ** Styles
import '@styles/react/apps/app-users.scss'
import { Fragment, useEffect, useRef, useState } from 'react'
import { Badge, Button, Card, CardBody, Col, Row, Table } from 'reactstrap'
import CardAttendance from '../../ui-elements/cards/advance/CardAttendance'
import moment from 'moment'
import { Link } from 'react-router-dom'
import axios from '@src/configs/axios/axiosConfig'
import { orgUserId, activeOrganization } from '@src/helper/sassHelper'
import { ChevronLeft, ChevronRight } from 'react-feather'
import Attendance from '@src/views/apps/profile/Attendance.js'
import { selectThemeColors } from '@utils'
import Select from 'react-select'
import '@styles/react/libs/react-select/_react-select.scss'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const userId = orgUserId()
import listPlugin from '@fullcalendar/list'

const activeOrg = activeOrganization()
const TeamList = () => {

  const calendarRef = useRef(null)
  const [data, setData] = useState([])
  const [orguserid, setOrgUserId] = useState(userId)
  const [selectedUserName, setSelectedUserName] = useState('')
  const [employeeOptions, setEmployeeOptions] = useState([])
  const [todayPunchin, setTodayPunchIn] = useState('')
  const [clockRunningTime, setClockRunningTime] = useState('00:00')
  // ** States
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const getAttendance = (userid, stdate = null) => {
    setBlock(true)
    axios.post('employeeAttendances/attendancecalendar', { userId: userid, date: stdate ? moment(stdate).startOf('m').endOf('D').valueOf() : moment().valueOf() })
      .then((res) => {
        setData(res.data.employeeattendances)
        setBlock(false)
      })
      .catch(() => { setBlock(false) })
  }

  const statusColorArr = ['', '#7367f01f', '#28c76f1f', '#ff9f431f']
  const statusFontColorArr = ['', 'text-primary', 'text-success', 'text-warning']

  const msToTime = (ms) => {

    let seconds = ms / 1000
    const hours = parseInt(seconds / 3600)
    seconds = seconds % 3600
    const minutes = parseInt(seconds / 60)
    seconds = seconds % 60
    return `${hours} : ${minutes}: ${parseInt(seconds)}`
  }

  const clockcounter = () => {
    if (todayPunchin) {
      const now = moment()
      const ms = moment.utc(moment(now, "DD/MM/YYYY HH:mm:ss").diff(moment(todayPunchin, "DD/MM/YYYY HH:mm:ss")))
      setClockRunningTime(msToTime(ms))

    }
  }

  useEffect(() => {
    if (todayPunchin) {
      setInterval(() => {
        clockcounter()
      }, [1000])
    }
  }, [todayPunchin])

  const todaydate = moment().startOf('day').utc().format('x')
  const renderEventContent = (arg) => {

    if (todaydate === arg.event.extendedProps.actualdate) {
      const punchin = moment(arg.event.extendedProps.intime, 'x').format('DD/MM/YYYY HH:mm:ss')
      setTodayPunchIn(punchin)
    }

    const status = arg.event.extendedProps.status
    arg.backgroundColor = arg.event.extendedProps.status !== null ? statusColorArr[status] : '#ea54551f'
    arg.borderColor = arg.event.extendedProps.status !== null ? statusColorArr[status] : '#ea54551f'

    const italicEl = document.createElement('div')
    italicEl.innerHTML = `
        <span class='${statusFontColorArr[status] || 'text-danger'}'>                 
          ${todaydate === arg.event.extendedProps.actualdate ? clockRunningTime : moment(arg.event.extendedProps.intime, 'x').format('hh:mm A')}
          </br>
          ${arg.event.title}
        </span>`

    const arrayOfDomNodes = [italicEl]
    return { domNodes: arrayOfDomNodes }

  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data.organizationusers
      setEmployeeOptions(arr)
      setSelectedUserName(arr.find((oj) => oj.id === orguserid)?.name)
    }).catch((err) => {
      console.log(err)
    })
  }


  useEffect(async () => {
    getAttendance(orguserid)
    getOrganizationUsers()


  }, [])

  const [width, setWidth] = useState(window.innerWidth)
  const handleWindowSizeChange = () => {
    setWidth(window.innerWidth)
  }
  useEffect(() => {
    window.addEventListener('resize', handleWindowSizeChange)
    return () => {
      window.removeEventListener('resize', handleWindowSizeChange)
    }
  }, [])

  const calendarOptions = {
    events: data,
    plugins: [dayGridPlugin, listPlugin],
    initialView: width >= 768 ? 'dayGridMonth' : 'listMonth',
    headerToolbar: {
      start: 'customprev,customnext',
      center: 'title',
      end: ''
    },
    eventContent: renderEventContent,
    /*
      Enable dragging and resizing event
      ? Docs: https://fullcalendar.io/docs/editable
    */
    editable: true,
    displayEventTime: false,
    eventDisplay: 'block',
    /*
      Enable resizing event from start
      ? Docs: https://fullcalendar.io/docs/eventResizableFromStart
    */
    eventResizableFromStart: true,

    /*
      Automatically scroll the scroll-containers during event drag-and-drop and date selecting
      ? Docs: https://fullcalendar.io/docs/dragScroll
    */
    dragScroll: true,

    /*
      Max number of events within a given day
      ? Docs: https://fullcalendar.io/docs/dayMaxEvents
    */
    dayMaxEvents: 2,

    /*
      Determines if day names and week names are clickable
      ? Docs: https://fullcalendar.io/docs/navLinks
    */
    navLinks: true,

    // eventClassNames({ event: calendarEvent }) {
    //   // eslint-disable-next-line no-underscore-dangle
    //   const colorName = calendarsColor[calendarEvent._def.extendedProps.extendedprops.calendar]

    //   return [
    //     // Background Color
    //     `bg-light-${colorName}`
    //   ]
    // },
    customButtons: {
      customprev: {
        text: <ChevronLeft />,
        click: () => {
          const calref = calendarRef.current.getApi()
          calref.prev()
          getAttendance(orguserid, calref.getDate())
        }
      },
      customnext: {
        text: <ChevronRight />,
        click: () => {
          const calref = calendarRef.current.getApi()
          calref.next()
          getAttendance(orguserid, calref.getDate())
        }
      }
    },
    fixedWeekCount: false,
    eventClick({ event: clickedEvent }) {
      console.log(clickedEvent)
      // * Only grab required field otherwise it goes in infinity loop
      // ! Always grab all fields rendered by form (even if it get `undefined`) otherwise due to Vue3/Composition API you might get: "object is not extensible"
      // event.value = grabEventDataFromEventApi(clickedEvent)

      // eslint-disable-next-line no-use-before-define
      // isAddNewEventSidebarActive.value = true
    },
    ref: calendarRef,
    // Get direction from app state (store)
    direction: 'ltr'
  }

  return (
    <Fragment>
      <Card className='shadow-none border-0 mb-0 rounded-0'>
        <CardBody className='pb-0' >
          <UILoader blocking={block} loader={<Loader />}>
            <Row>
              <Col lg={5}>
                {(parseInt(activeOrg.rolelevel) === 1 || parseInt(activeOrg.rolelevel) === 2) &&
                  <Col className='mb-1 d-flex align-items-center justify-content-between'>
                    <h5 className='font-medium-3'>{selectedUserName}</h5>
                    <Select
                      isClearable={false}
                      value={employeeOptions.find((obj) => obj.id === orguserid)}
                      options={employeeOptions}
                      className='react-select col-sm-12 col-md-6'
                      classNamePrefix='select'
                      theme={selectThemeColors}
                      getOptionLabel={(option) => option.name}
                      getOptionValue={(option) => option.id}
                      onChange={async (data) => {
                        setOrgUserId(data.id)
                        const calref = calendarRef.current.getApi()
                        getAttendance(data.id, calref.getCurrentData().currentDate)
                        setSelectedUserName(data.name)
                      }}
                    />
                  </Col>
                }
                {userId === orguserid &&
                  <Col className='border rounded p-1'>

                    <h5>Mark attendance for today ({moment().format('DD MMM, Y')})</h5>
                    <CardAttendance margin={['mt-1 mb-0', 'mb-0']} padding='px-0' welcomeflag={true} />
                  </Col>
                }
                <Col className='border rounded p-1 mt-1'>
                  <h5>Your leave balance</h5>
                  <Link to={`/leaves/list`}>
                    <Badge color='light-primary'>
                      Click here to View Leave Summary</Badge>
                  </Link>
                </Col>
              </Col>
              <Col lg={1} sm={0}></Col>
              <Col lg={6} className='pb-2 mt-1 mt-lg-0'>
                <FullCalendar {...calendarOptions} themeSystem="bootstrap" height={500} />{' '}
              </Col>
            </Row>
          </UILoader>
        </CardBody>
      </Card>
      <Card className='mt-2'>
        <CardBody>
          <Row>
            <h5>Attendance List</h5>
            <Attendance tabId='profileattendance' selectedUserName={selectedUserName} userId={orguserid} />
          </Row>
        </CardBody >
      </Card >
    </Fragment>
  )
}

export default TeamList
