// ** Custom Components
import Avatar from '@components/avatar'
import moment from 'moment'
import { Badge, Button } from 'reactstrap'

// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.name?.charAt(0) || 'Jhone'}
    />
  )

}

const statusArr = ['', 'Early', 'On Time', 'Late']
const statusColorArr = ['', 'light-primary', 'light-success', 'light-warning']
const dateFormat = (value) => {
  return moment(value, 'x').format("H:m A")
}

const msToTime = (ms) => {

  let seconds = ms / 1000
  const hours = parseInt(seconds / 3600)
  seconds = seconds % 3600
  const minutes = parseInt(seconds / 60)
  seconds = seconds % 60
  return `${hours} Hrs : ${minutes} Mins`
}

export const columns = [
  {
    name: 'Team Members',
    sortable: true,
    maxWidth: '250px',
    minWidth: '250px',
    sortField: 'username',
    selector: row => row.name,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <small className='text-truncate user_name text-body mb-0'>{row.name}</small>
        </div>
      </div>
    )
  },
  {
    name: 'Clock In',
    sortable: true,
    maxWidth: '142px',
    sortField: 'punchin',
    cell: row => <span>{(row.punchin !== null) ? dateFormat(row.punchin) : 0}</span>
  },
  {
    name: 'Clock Out',
    maxWidth: '150px',
    sortable: true,
    sortField: 'punchout',
    cell: row => <span>{(row.punchout !== null) ? dateFormat(row.punchout) : 0}</span>
  },
  {
    name: 'Running Time',
    maxWidth: '230px',
    sortable: true,
    sortField: 'department',
    selector: row => row.department,
    cell: row => <span className='text-capitalize'>{msToTime(row.totalhours) || 0}</span>
  },
  {
    name: 'Location',
    minWidth: '130px',
    sortable: true,
    sortField: 'location',
    selector: row => row.locationname,
    cell: row => (

      <span className='text-capitalize text-truncate'>
        <a href={`https://www.google.com/maps/search/${row?.locationname}`} target='_blank'>{row.locationname}</a>
      </span>
    )
  },
  {
    name: 'Location',
    minWidth: '130px',
    sortable: true,
    sortField: 'location',
    selector: row => row.punchoutlocation,
    cell: row => (

      <span className='text-capitalize text-truncate'>
        <a href={`https://www.google.com/maps/search/${row?.punchoutlocation}`} target='_blank'>{row.punchoutlocation}</a>
      </span>
    )
  },
  {
    name: 'Status',
    maxWidth: '120px',
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => <Badge color={`${statusColorArr[row.status]}`}>{statusArr[row.status]} </Badge>
  }
]
