// ** Reactstrap Imports
import { Card, CardBody, CardText, Button, Row, Col, Badge, Modal, ModalHeader, ModalBody, ModalFooter, Input } from 'reactstrap'
// ** Images
//import medal from '@src/assets/images/illustration/badge.svg'
import { LogIn, LogOut, MapPin } from 'react-feather'
import { endTimer } from '@src/views/apps/task/store/index.js'
// ** Custom Components
import { activeOrganizationid, orgUserId, loggedInUser } from '@src/helper/sassHelper'
import Avatar from '@components/avatar'
import { punchIn, punchOut, getAttendance } from '../../../dashboard/ecommerce/store'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment'
import axios from '@src/configs/axios/axiosConfig'
import { geoLocation } from '@src/views/pages/account-settings/store/index.js'
import { Link, useNavigate } from 'react-router-dom'
import { Can } from '@src/utility/context/Can.js'
import toast from 'react-hot-toast'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const userData = loggedInUser()
const CardAttendance = ({ margin, padding, welcomeflag }) => {

  const attStatus = ['', 'Early', 'Regular', 'Late']
  const atttatusColor = ['', 'light-primary', 'light-success', 'light-danger']
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const dayName = moment().format('dddd')
  const store = useSelector(state => state.dashboard)
  const orgstore = useSelector(state => state.organization)
  const notstore = useSelector(state => state.notifications)

  const [loggedIn, setLoggedIn] = useState(false)
  const [data, setData] = useState(null)
  const [businessHours, setBusinessHours] = useState([])
  const [graceTime, setGraceTime] = useState(0.00)
  const [punchInTime, setPunchInTime] = useState(null)
  const [punchInTimeWithDate, setPunchInTimeWithDate] = useState(null)
  const [clockRunningTime, setClockRunningTime] = useState(null)
  const [punchOutTime, setPunchoutTime] = useState(null)
  const [hourlyRate, setHourlyRate] = useState(0)
  const [locationName, setLocationname] = useState('')

  const [formModal, setFormModal] = useState(false)
  const [breakName, setBreakName] = useState('')
  const [breakCon, setBreakCon] = useState(null)

  const item_login = {
    color: 'light-success',
    icon: <LogIn size={24} />
  }

  const item_logout = {
    color: 'light-danger',
    icon: <LogOut size={24} />
  }

  const getusinessHours = () => {
    axios.post('/organizationprefernces/getbusinesshours')
      .then((res) => {
        setBusinessHours(res.data.organizationprefernces.officetime)
        const temp = parseFloat(res.data.organizationprefernces.punchingrace) < 10 ? parseFloat(res.data.organizationprefernces.punchingrace / 10).toFixed(2) : parseFloat(res.data.organizationprefernces.punchingrace / 100).toFixed(2)
        setGraceTime(temp)
      })
      .catch(() => { })
  }


  const getEmployeeDet = () => {
    axios.post('/organizationusers/getjobinfo', { id: userId })
      .then((res) => {
        setHourlyRate(res.data.organizationusers.hourlyrate || 0)
      }).catch((err) => {
        console.log(err)
      })
  }

  const sendNotification = (data) => {

    axios.post('/notifications/sendnotifications', { data })

  }

  useEffect(() => {

    if (userId === notstore?.message?.userid) {
      setData(notstore.message)
      setLocationname(notstore.message.locationname)
    }

  }, [notstore.message])

  const updateLocation = (location, punchoutLoc, id) => {

    axios.post('/employeeattendances/updatelocation', { id, punchoutlocation: punchoutLoc, location })
      .then(async () => {
      })
  }

  const getLocation = async (latitude, longitude, flg = true, id) => {
    const request = new XMLHttpRequest()

    const method = 'GET'
    const url = `https://maps.googleapis.com/maps/api/geocode/json?latlng=${latitude},${longitude}&sensor=false&key=AIzaSyB1Mx2YS1sVJqU-K6jaFFX2epPPB4djnSU`
    const async = true
    let location = ''
    await request.open(method, url, async)
    request.onreadystatechange = async () => {
      if (request.readyState === 4 && request.status === 200) {
        const data = JSON.parse(request.responseText)
        const address = data.results[0]
        location = address.formatted_address
        if (flg) {
          setLocationname(location)
          updateLocation(location, '', id)
        } else {
          updateLocation('', location, id)
        }
      }
    }
    await request.send()

  }

  const punchInfn = async () => {
    const businessHour = businessHours.find((obj) => {
      return JSON.parse(obj).day === dayName
    })
    const Dayhours = JSON.parse(businessHour)
    let status = 2
    const currentTime = moment().format('H.mm')
    const temptime = (Dayhours.start.split(':')).join('.')
    if ((Number(temptime) + Number(parseFloat(graceTime).toFixed(2))) < Number(currentTime)) {
      status = 3
    } else if ((Number(temptime) - Number(parseFloat(graceTime).toFixed(2))) > Number(currentTime)) {
      status = 1
    }

    const data = {
      organizationId: activeOrgId,
      punchIn: moment().utc().valueOf(),
      userId,
      status,
      createdBy: userId,
      attendancelatitude: orgstore?.geoCordinate?.latitude,
      attendancelangtitude: orgstore?.geoCordinate?.longitude,
      locationName,
      actualDate: moment().startOf('day').utc().format('x')
    }
    const res = await dispatch(punchIn(data))

    if (Object.keys(orgstore).length > 0) {
      await getLocation(orgstore?.geoCordinate?.latitude, orgstore?.geoCordinate?.longitude, true, res.payload?.id)
    }

    await sendNotification(res.payload)
  }

  const msToTime = (ms) => {

    let seconds = ms / 1000
    const hours = parseInt(seconds / 3600)
    seconds = seconds % 3600
    const minutes = parseInt(seconds / 60)
    seconds = seconds % 60
    return `${hours} : ${minutes}: ${parseInt(seconds)}`
  }

  const clockcounter = () => {
    if (punchInTimeWithDate !== undefined && punchInTimeWithDate !== null) {
      const now = moment()
      const ms = moment.utc(moment(now, "DD/MM/YYYY HH:mm:ss").diff(moment(punchInTimeWithDate, "DD/MM/YYYY HH:mm:ss")))
      setClockRunningTime(msToTime(ms))
    }
  }

  const stopTaskTimer = async (temp) => {
    const end = moment()
    const start = moment(temp.starttime, 'x').toDate()
    const end_timestamp = moment().valueOf()
    const duration = moment.duration(end.diff(start))
    const hours = duration.asHours()
    const amount = hourlyRate * hours
    const data = {
      updatedBy: userId,
      id: temp.id,
      taskId: temp.taskid,
      totalhours: hours,
      amount,
      hourlyRate,
      userId,
      endTime: end_timestamp
    }
    await dispatch(endTimer(data))
    await localStorage.removeItem('currentTask')
  }

  const punchoutfn = async () => {

    if (breakCon) {
      toast.error('Please Stop Break Time', { position: 'bottom-right' })
      return false
    }

    const now = moment()
    const ms = moment(now, "DD/MM/YYYY HH:mm:ss").diff(moment(punchInTimeWithDate, "DD/MM/YYYY HH:mm:ss"))

    const datatemp = {
      organizationId: activeOrgId,
      punchOut: moment().valueOf(),
      userId,
      id: data.id,
      updatedBy: userId,
      totalHours: ms
    }
    await dispatch(punchOut(datatemp))

    const tasktimer = JSON.parse(localStorage.getItem('currentTask') || null)
    if (tasktimer !== null) {
      stopTaskTimer(tasktimer)
    }

    navigator.geolocation.getCurrentPosition(
      async (position) => {
        const { latitude, longitude } = position.coords

        dispatch(geoLocation({ latitude, longitude }))
        await getLocation(latitude, longitude, false, data?.id)
      },
      (error) => {
        // display an error if we cant get the users position
        console.error('Error getting user location:', error)
      }
    )

  }

  useEffect(() => {
    if (punchInTimeWithDate !== undefined && punchInTimeWithDate !== null) {
      const now = moment()
      const ms = moment.utc(moment(now, "DD/MM/YYYY HH:mm:ss").diff(moment(punchInTimeWithDate, "DD/MM/YYYY HH:mm:ss")))
      setClockRunningTime(msToTime(ms))
    }
  }, [punchInTimeWithDate])

  useEffect(() => {
    const temp = JSON.parse(localStorage.getItem('loggedIn'))
    if (temp === null) {
      setLoggedIn(false)
    } else {
      setLoggedIn(true)
      setPunchoutTime(temp.punchout)
      setData(temp)
      setLocationname(temp.locationname)
      setPunchInTime(moment(temp.punchin, 'x').format('h:m a'))
      const tempdate = moment(temp.punchin, 'x').format('DD/MM/YYYY HH:mm:ss')
      setPunchInTimeWithDate(tempdate)
      if (temp.punchout) {
        setLoggedIn(false)
        setPunchoutTime(moment(temp.punchout, 'x').format('h:m a'))
      }

    }

  }, [])

  if (data) {
    setInterval(() => {
      clockcounter()
    }, 1000)
  }

  useEffect(() => {
    if (!!store && !!store.data && Object.keys(store.data).length > 0) {
      setLoggedIn(true)
      const temp = store.data
      setData(temp)
      setLocationname(temp.locationname)
      localStorage.setItem('loggedIn', JSON.stringify(temp))
      setPunchInTime(moment(temp.punchin, 'x').format('h:m a'))
      setPunchInTimeWithDate(moment(temp.punchin, 'x').format('DD/MM/YYYY HH:mm:ss'))
      if (temp.punchout) {
        setLoggedIn(false)
        setPunchoutTime(moment(temp.punchout, 'x').format('h:m a'))
      }

    } else {
      localStorage.removeItem('loggedIn')
      setData(null)
      setLoggedIn(false)
      setPunchoutTime(null)
      setPunchInTime(null)
      setPunchInTimeWithDate(null)
    }
  }, [store.data])

  useEffect(async () => {
    await dispatch(getAttendance({ userId, actualDate: moment().startOf('day').utc().format('x') }))
    getusinessHours()
    getEmployeeDet()
  }, [])


  useEffect(() => {
    const breakStorage = JSON.parse(localStorage.getItem('breakLogin'))
    if (breakStorage) {
      setBreakCon(breakStorage)
    }
  }, [])

  const [runningtime, setRunningTime] = useState(0)

  const breakRunningTime = () => {
    const now = moment().valueOf()
    const ms = moment.utc(moment(now, "x").diff(moment(breakCon.starttime, "x")))
    setRunningTime(msToTime(ms))
  }

  useEffect(() => {
    if (breakCon) {
      setInterval(() => {
        breakRunningTime()
      }, [1000])
    }
  }, [breakCon])

  const submitBreak = () => {
    const startTime = moment().valueOf()
    axios.post('/tasktimesheets/breakentry', {
      userId, createdBy: userId, starttime: startTime, name: breakName
    })
      .then((res) => {
        localStorage.setItem('breakLogin', JSON.stringify(res.data.timesheet))
        setBreakCon(res.data.timesheet)
        setFormModal(false)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  const endBreak = () => {
    const endTime = moment().valueOf()
    axios.post('/tasktimesheets/breakentry', {
      userId, createdBy: userId, starttime: breakCon?.starttime, id: breakCon?.id, endtime: endTime, name: breakCon.manualname
    })
      .then(() => {
        localStorage.removeItem('breakLogin')
        setBreakCon(null)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  return (
    <Card className={`card-congratulations-medal ${margin !== undefined ? margin[0] : ''}`}>
      <CardBody className={`flex ${padding}`}>
        <Row className='d-flex justify-content-between'>
          <Col className='col' md={6} sm={6}>

            {!welcomeflag && <h5>Welcome  {userData.name}!</h5>}
            <Col className='d-flex'>
              {
                businessHours !== null && businessHours.length > 0 && (
                  !loggedIn ? (<Button color='success' onClick={() => punchInfn()}>Punch In</Button>) : (<Button color='danger' onClick={() => punchoutfn()}>Punch Out</Button>)
                )
              }
            </Col>
            {
              (businessHours === null || businessHours.length === 0) && (
                <Can I={'read'} a={'setting'}>
                  <p className='cursor-pointer text-primary' onClick={() => navigate('/account-settings')}>Click here to set Business Hours</p>
                </Can>
              )
            }

            <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered modal-sm'>
              <ModalHeader toggle={() => setFormModal(!formModal)}>
                Break
              </ModalHeader>
              <ModalBody>
                <Input type='text' placeholder='Break Name' onChange={(e) => setBreakName(e.target.value)} />
              </ModalBody>
              <ModalFooter>
                <Button color='warning' onClick={() => setFormModal(formModal)} outline>
                  Cancel
                </Button>
                <Button color='primary' outline onClick={() => { submitBreak() }}>
                  Submit
                </Button>
              </ModalFooter>
            </Modal>
          </Col>
          {
            businessHours !== null && businessHours.length > 0 && (
              <Col className='col text-end' md='6' sm={6}>
                {data !== null &&
                  <div>
                    <Badge color={atttatusColor[data.status]}>{attStatus[data.status]}</Badge>
                  </div>
                }

                {(loggedIn && !breakCon) &&
                  <Button color='primary' onClick={() => setFormModal(true)} className='mt-50' outline>Take a Break</Button>
                }
                {(loggedIn && breakCon) &&
                  <Button color='success' onClick={() => endBreak()} className='mt-50' outline>End Break</Button>
                }
              </Col>
            )
          }

        </Row>
        <Row>
          {locationName !== '' &&
            <a className='d-flex mt-50' href={`https://www.google.com/maps/search/${locationName}`} target='_blank'>
              <div className='text-truncate text-primary font-small-3 width-200'>{locationName}</div>
              <MapPin size={16} className='text-primary' />
            </a>
          }
          {breakCon &&
            <h6 className='text-danger mt-50'>Break Running time : {runningtime} </h6>
          }
        </Row>
        <Row className={`pt-1`}>
          <Col className='d-flex align-items-center col' sm={6} lg={6}>
            <Avatar color={item_login.color} icon={item_login.icon} className='me-2 p-1' />
            <Col>
              <h6 className='fw-bolder mb-0'>Punch In</h6>
              <CardText className='font-small-3 mb-0'>{punchInTime === null ? '00:00:00' : punchInTime}</CardText>
            </Col>
          </Col>
          <Col className='d-flex align-items-center col' sm={6} lg={6}>
            <Avatar color={item_logout.color} icon={item_logout.icon} className='me-2 p-1' />
            <Col>
              <h6 className='fw-bolder mb-0'>Punch Out</h6>
              <CardText className='font-small-3 mb-0'>{punchOutTime === null ? clockRunningTime : punchOutTime}</CardText>
            </Col>
          </Col>
        </Row>
      </CardBody>
    </Card >
  )
}

export default CardAttendance
